@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900');
@import url('https://fonts.googleapis.com/css?family=Kanit:400,500,600,700,800,900');

:root {
  --background-color: #F8F8F8;
  --font-primary-color: #000000;
  --font-secondary-color: #484850;
  --primary-color: #2FA31C;
  --secondary-color: #769871;
  --border-color: #eeeeee;
  --shadow-color: 0px 2px 8px 0px rgba(0, 0, 0, 0.04);
}

@media (max-width: 1200px) {
  .footer-container {
    padding-bottom: 72px !important;
  }
}

.font-super {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 48px !important;
  line-height: 64px !important;
  transition: all 0.5s ease;
}

.font-header {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 24px !important;
  line-height: 36px !important;
  transition: all 0.5s ease;
}

.font-subheader {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 20px !important;
  line-height: 30px !important;
  transition: all 0.5s ease;
}

.font-body {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 400 !important;
  font-size: 14px !important;
  line-height: 24px !important;
  transition: all 0.5s ease;
}

.font-bodybold {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 14px !important;
  line-height: 24px !important;
  transition: all 0.5s ease;
}

.font-subbody {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 400 !important;
  font-size: 12px !important;
  line-height: 21px !important;
  transition: all 0.5s ease;
}

.font-subbodybold {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 12px !important;
  line-height: 21px !important;
  transition: all 0.5s ease;
}

.font-highlight {
  font-family: 'Kalam';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  transition: all 0.5s ease;
}

.font-highlightbold {
  font-family: 'Kalam';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  transition: all 0.5s ease;
}

.font-tag {
  font-family: 'Poppins','Kanit' !important;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 16px;
}

.color-fontblack {
  color: var(--font-primary-color);
  transition: all 0.5s ease !important;
}

.color-fontgrey {
  color: var(--font-secondary-color);
  transition: all 0.5s ease !important;
}

.color-fontmain {
  color: var(--primary-color);
  transition: all 0.5s ease !important;
}

.color-fontsub {
  color: var(--secondary-color);
  transition: all 0.5s ease !important;
}

.color-fontselect {
  color: var(--primary-color);
  transition: all 0.5s ease !important;
}

.color-background {
  background-color: var(--background-color) !important;
  background: var(--background-color) !important;
  transition: all 0.5s ease !important;
}

.scroll-margin {
  scroll-margin: 80px;
}

.fixed-bar {
  background: var(--surface-color);
  transition: all 0.5s ease;
}

.navbar-container {
  height: 56px;
  margin: auto;
  transition: all 0.5s ease;
}

.BottomNavBar-container {
  height: 56px;
  margin: auto;
  transition: all 0.5s ease;
}

.footer-container {
  padding-top: 16px;
  padding-bottom: 16px;
  transition: all 0.5s ease;
}

.global-top {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  transition: all 0.5s ease;
}

.global-bottom {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 9999;
}

.footer {
  width: 100%;
}